/*********************************************
 * GLOBAL STYLES
 *********************************************/

html {
  width: 100%;
  height: 100%;
  height: 100vh;
  height: calc( var(--vh, 1vh) * 100 );
  overflow: hidden;
}

body {
  height: 100%;
  overflow: hidden;
  position: relative;
  line-height: 1;
  margin: 0;

  background-color: #fff;
  color: #000;
}


/*********************************************
 * VIEW FRAGMENTS
 *********************************************/

.reveal .slides section .fragment {
  opacity: 0;
  visibility: hidden;
  transition: all .2s ease;

  &.visible {
    opacity: 1;
    visibility: inherit;
  }
}

.reveal .slides section .fragment.grow {
  opacity: 1;
  visibility: inherit;

  &.visible {
    transform: scale( 1.3 );
  }
}

.reveal .slides section .fragment.shrink {
  opacity: 1;
  visibility: inherit;

  &.visible {
    transform: scale( 0.7 );
  }
}

.reveal .slides section .fragment.zoom-in {
  transform: scale( 0.1 );

  &.visible {
    transform: none;
  }
}

.reveal .slides section .fragment.fade-out {
  opacity: 1;
  visibility: inherit;

  &.visible {
    opacity: 0;
    visibility: hidden;
  }
}

.reveal .slides section .fragment.semi-fade-out {
  opacity: 1;
  visibility: inherit;

  &.visible {
    opacity: 0.5;
    visibility: inherit;
  }
}

.reveal .slides section .fragment.strike {
  opacity: 1;
  visibility: inherit;

  &.visible {
    text-decoration: line-through;
  }
}

.reveal .slides section .fragment.fade-up {
  transform: translate(0, 20%);

  &.visible {
    transform: translate(0, 0);
  }
}

.reveal .slides section .fragment.fade-down {
  transform: translate(0, -20%);

  &.visible {
    transform: translate(0, 0);
  }
}

.reveal .slides section .fragment.fade-right {
  transform: translate(-20%, 0);

  &.visible {
    transform: translate(0, 0);
  }
}

.reveal .slides section .fragment.fade-left {
  transform: translate(20%, 0);

  &.visible {
    transform: translate(0, 0);
  }
}

.reveal .slides section .fragment.fade-in-then-out,
.reveal .slides section .fragment.current-visible {
  opacity: 0;
  visibility: hidden;

  &.current-fragment {
    opacity: 1;
    visibility: inherit;
  }
}

.reveal .slides section .fragment.fade-in-then-semi-out {
  opacity: 0;
  visibility: hidden;

  &.visible {
    opacity: 0.5;
    visibility: inherit;
  }

  &.current-fragment {
    opacity: 1;
    visibility: inherit;
  }
}

.reveal .slides section .fragment.highlight-red,
.reveal .slides section .fragment.highlight-current-red,
.reveal .slides section .fragment.highlight-green,
.reveal .slides section .fragment.highlight-current-green,
.reveal .slides section .fragment.highlight-blue,
.reveal .slides section .fragment.highlight-current-blue {
  opacity: 1;
  visibility: inherit;
}
.reveal .slides section .fragment.highlight-red.visible {
  color: #ff2c2d
}
.reveal .slides section .fragment.highlight-green.visible {
  color: #17ff2e;
}
.reveal .slides section .fragment.highlight-blue.visible {
  color: #1b91ff;
}

.reveal .slides section .fragment.highlight-current-red.current-fragment {
  color: #ff2c2d
}
.reveal .slides section .fragment.highlight-current-green.current-fragment {
  color: #17ff2e;
}
.reveal .slides section .fragment.highlight-current-blue.current-fragment {
  color: #1b91ff;
}


/*********************************************
 * DEFAULT ELEMENT STYLES
 *********************************************/

/* Fixes issue in Chrome where italic fonts did not appear when printing to PDF */
.reveal:after {
  content: '';
  font-style: italic;
}

.reveal iframe {
  z-index: 1;
}

/** Prevents layering issues in certain browser/transition combinations */
.reveal a {
  position: relative;
}

.reveal .stretch {
  max-width: none;
  max-height: none;
}

.reveal pre.stretch code {
  height: 100%;
  max-height: 100%;
  box-sizing: border-box;
}


/*********************************************
 * CONTROLS
 *********************************************/

@keyframes bounce-right {
  0%, 10%, 25%, 40%, 50% {transform: translateX(0);}
  20% {transform: translateX(10px);}
  30% {transform: translateX(-5px);}
}

@keyframes bounce-down {
  0%, 10%, 25%, 40%, 50% {transform: translateY(0);}
  20% {transform: translateY(10px);}
  30% {transform: translateY(-5px);}
}

$controlArrowSize: 3.6em;
$controlArrowSpacing: 1.4em;
$controlArrowLength: 2.6em;
$controlArrowThickness: 0.5em;
$controlsArrowAngle: 45deg;
$controlsArrowAngleHover: 40deg;
$controlsArrowAngleActive: 36deg;

@mixin controlsArrowTransform( $angle ) {
  &:before {
    transform: translateX(($controlArrowSize - $controlArrowLength)/2) translateY(($controlArrowSize - $controlArrowThickness)/2) rotate( $angle );
  }

  &:after {
    transform: translateX(($controlArrowSize - $controlArrowLength)/2) translateY(($controlArrowSize - $controlArrowThickness)/2) rotate( -$angle );
  }
}

.reveal .controls {
  $spacing: 12px;

  display: none;
  position: absolute;
  top: auto;
  bottom: $spacing;
  right: $spacing;
  left: auto;
  z-index: 1;
  color: #000;
  pointer-events: none;
  font-size: 10px;

  button {
    position: absolute;
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
    color: currentColor;
    transform: scale(.9999);
    transition: color 0.2s ease,
    opacity 0.2s ease,
    transform 0.2s ease;
    z-index: 2; // above slides
    pointer-events: auto;
    font-size: inherit;

    visibility: hidden;
    opacity: 0;

    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba( 0, 0, 0, 0 );
  }

  .controls-arrow:before,
  .controls-arrow:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: $controlArrowLength;
    height: $controlArrowThickness;
    border-radius: $controlArrowThickness/2;
    background-color: currentColor;

    transition: all 0.15s ease, background-color 0.8s ease;
    transform-origin: floor(($controlArrowThickness/2)*10)/10 50%;
    will-change: transform;
  }

  .controls-arrow {
    position: relative;
    width: $controlArrowSize;
    height: $controlArrowSize;

    @include controlsArrowTransform( $controlsArrowAngle );

    &:hover {
      @include controlsArrowTransform( $controlsArrowAngleHover );
    }

    &:active {
      @include controlsArrowTransform( $controlsArrowAngleActive );
    }
  }

  .navigate-left {
    right: $controlArrowSize + $controlArrowSpacing*2;
    bottom: $controlArrowSpacing + $controlArrowSize/2;
    transform: translateX( -10px );
  }

  .navigate-right {
    right: 0;
    bottom: $controlArrowSpacing + $controlArrowSize/2;
    transform: translateX( 10px );

    .controls-arrow {
      transform: rotate( 180deg );
    }

    &.highlight {
      animation: bounce-right 2s 50 both ease-out;
    }
  }

  .navigate-up {
    right: $controlArrowSpacing + $controlArrowSize/2;
    bottom: $controlArrowSpacing*2 + $controlArrowSize;
    transform: translateY( -10px );

    .controls-arrow {
      transform: rotate( 90deg );
    }
  }

  .navigate-down {
    right: $controlArrowSpacing + $controlArrowSize/2;
    bottom: 0;
    transform: translateY( 10px );

    .controls-arrow {
      transform: rotate( -90deg );
    }

    &.highlight {
      animation: bounce-down 2s 50 both ease-out;
    }
  }

  // Back arrow style: "faded":
  // Deemphasize backwards navigation arrows in favor of drawing
  // attention to forwards navigation
  &[data-controls-back-arrows="faded"] .navigate-left.enabled,
  &[data-controls-back-arrows="faded"] .navigate-up.enabled {
    opacity: 0.3;

    &:hover {
      opacity: 1;
    }
  }

  // Back arrow style: "hidden":
  // Never show arrows for backwards navigation
  &[data-controls-back-arrows="hidden"] .navigate-left.enabled,
  &[data-controls-back-arrows="hidden"] .navigate-up.enabled {
    opacity: 0;
    visibility: hidden;
  }

  // Any control button that can be clicked is "enabled"
  .enabled {
    visibility: visible;
    opacity: 0.9;
    cursor: pointer;
    transform: none;
  }

  // Any control button that leads to showing or hiding
  // a fragment
  .enabled.fragmented {
    opacity: 0.5;
  }

  .enabled:hover,
  .enabled.fragmented:hover {
    opacity: 1;
  }
}

.reveal[data-navigation-mode="linear"].has-horizontal-slides .navigate-up,
.reveal[data-navigation-mode="linear"].has-horizontal-slides .navigate-down {
  display: none;
}

// Adjust the layout when there are no vertical slides
.reveal[data-navigation-mode="linear"].has-horizontal-slides .navigate-left,
.reveal:not(.has-vertical-slides) .controls .navigate-left {
  bottom: $controlArrowSpacing;
  right: 0.5em + $controlArrowSpacing + $controlArrowSize;
}

.reveal[data-navigation-mode="linear"].has-horizontal-slides .navigate-right,
.reveal:not(.has-vertical-slides) .controls .navigate-right {
  bottom: $controlArrowSpacing;
  right: 0.5em;
}

// Adjust the layout when there are no horizontal slides
.reveal:not(.has-horizontal-slides) .controls .navigate-up {
  right: $controlArrowSpacing;
  bottom: $controlArrowSpacing + $controlArrowSize;
}
.reveal:not(.has-horizontal-slides) .controls .navigate-down {
  right: $controlArrowSpacing;
  bottom: 0.5em;
}

// Invert arrows based on background color
.reveal.has-dark-background .controls {
  color: #fff;
}
.reveal.has-light-background .controls {
  color: #000;
}

// Disable active states on touch devices
.reveal.no-hover .controls .controls-arrow:hover,
.reveal.no-hover .controls .controls-arrow:active {
  @include controlsArrowTransform( $controlsArrowAngle );
}

// Edge aligned controls layout
@media screen and (min-width: 500px) {

  $spacing: 8px;

  .reveal .controls[data-controls-layout="edges"] {
    & {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }

    .navigate-left,
    .navigate-right,
    .navigate-up,
    .navigate-down {
      bottom: auto;
      right: auto;
    }

    .navigate-left {
      top: 50%;
      left: $spacing;
      margin-top: -$controlArrowSize/2;
    }

    .navigate-right {
      top: 50%;
      right: $spacing;
      margin-top: -$controlArrowSize/2;
    }

    .navigate-up {
      top: $spacing;
      left: 50%;
      margin-left: -$controlArrowSize/2;
    }

    .navigate-down {
      bottom: $spacing;
      left: 50%;
      margin-left: -$controlArrowSize/2;
    }
  }

}


/*********************************************
 * PROGRESS BAR
 *********************************************/

.reveal .progress {
  position: absolute;
  display: none;
  height: 3px;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 10;

  background-color: rgba( 0, 0, 0, 0.2 );
  color: #fff;
}
.reveal .progress:after {
  content: '';
  display: block;
  position: absolute;
  height: 10px;
  width: 100%;
  top: -10px;
}
.reveal .progress span {
  display: block;
  height: 100%;
  width: 0px;

  background-color: currentColor;
  transition: width 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985);
}

/*********************************************
 * SLIDE NUMBER
 *********************************************/

.reveal .slide-number {
  position: absolute;
  display: block;
  right: 8px;
  bottom: 8px;
  z-index: 31;
  font-family: Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1;
  color: #fff;
  background-color: rgba( 0, 0, 0, 0.4 );
  padding: 5px;
}

.reveal .slide-number a {
  color: currentColor;
}

.reveal .slide-number-delimiter {
  margin: 0 3px;
}

/*********************************************
 * SLIDES
 *********************************************/

.reveal {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  touch-action: pinch-zoom;
}

.reveal .slides {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  pointer-events: none;

  overflow: visible;
  z-index: 1;
  text-align: center;
  perspective: 600px;
  perspective-origin: 50% 40%;
}

.reveal .slides>section {
  perspective: 600px;
}

.reveal .slides>section,
.reveal .slides>section>section {
  display: none;
  position: absolute;
  width: 100%;
  padding: 20px 0px;
  pointer-events: auto;

  z-index: 10;
  transform-style: flat;
  transition: transform-origin 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985),
  transform 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985),
  visibility 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985),
  opacity 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985);
}

/* Global transition speed settings */
.reveal[data-transition-speed="fast"] .slides section {
  transition-duration: 400ms;
}
.reveal[data-transition-speed="slow"] .slides section {
  transition-duration: 1200ms;
}

/* Slide-specific transition speed overrides */
.reveal .slides section[data-transition-speed="fast"] {
  transition-duration: 400ms;
}
.reveal .slides section[data-transition-speed="slow"] {
  transition-duration: 1200ms;
}

.reveal .slides>section.stack {
  padding-top: 0;
  padding-bottom: 0;
  pointer-events: none;
  height: 100%;
}

.reveal .slides>section.present,
.reveal .slides>section>section.present {
  display: block;
  z-index: 11;
  opacity: 1;
}

.reveal .slides>section:empty,
.reveal .slides>section>section:empty,
.reveal .slides>section[data-background-interactive],
.reveal .slides>section>section[data-background-interactive] {
  pointer-events: none;
}

.reveal.center,
.reveal.center .slides,
.reveal.center .slides section {
  min-height: 0 !important;
}

/* Don't allow interaction with invisible slides */
.reveal .slides>section.future,
.reveal .slides>section>section.future,
.reveal .slides>section.past,
.reveal .slides>section>section.past {
  pointer-events: none;
}

.reveal.overview .slides>section,
.reveal.overview .slides>section>section {
  pointer-events: auto;
}

.reveal .slides>section.past,
.reveal .slides>section.future,
.reveal .slides>section>section.past,
.reveal .slides>section>section.future {
  opacity: 0;
}


/*********************************************
 * Mixins for readability of transitions
 *********************************************/

@mixin transition-global($style) {
  .reveal .slides section[data-transition=#{$style}],
  .reveal.#{$style} .slides section:not([data-transition]) {
    @content;
  }
}
@mixin transition-stack($style) {
  .reveal .slides section[data-transition=#{$style}].stack,
  .reveal.#{$style} .slides section.stack {
    @content;
  }
}
@mixin transition-horizontal-past($style) {
  .reveal .slides>section[data-transition=#{$style}].past,
  .reveal .slides>section[data-transition~=#{$style}-out].past,
  .reveal.#{$style} .slides>section:not([data-transition]).past {
    @content;
  }
}
@mixin transition-horizontal-future($style) {
  .reveal .slides>section[data-transition=#{$style}].future,
  .reveal .slides>section[data-transition~=#{$style}-in].future,
  .reveal.#{$style} .slides>section:not([data-transition]).future {
    @content;
  }
}

@mixin transition-vertical-past($style) {
  .reveal .slides>section>section[data-transition=#{$style}].past,
  .reveal .slides>section>section[data-transition~=#{$style}-out].past,
  .reveal.#{$style} .slides>section>section:not([data-transition]).past {
    @content;
  }
}
@mixin transition-vertical-future($style) {
  .reveal .slides>section>section[data-transition=#{$style}].future,
  .reveal .slides>section>section[data-transition~=#{$style}-in].future,
  .reveal.#{$style} .slides>section>section:not([data-transition]).future {
    @content;
  }
}

/*********************************************
 * SLIDE TRANSITION
 * Aliased 'linear' for backwards compatibility
 *********************************************/

@each $stylename in slide, linear {
  .reveal.#{$stylename} section {
    backface-visibility: hidden;
  }
  @include transition-horizontal-past(#{$stylename}) {
    transform: translate(-150%, 0);
  }
  @include transition-horizontal-future(#{$stylename}) {
    transform: translate(150%, 0);
  }
  @include transition-vertical-past(#{$stylename}) {
    transform: translate(0, -150%);
  }
  @include transition-vertical-future(#{$stylename}) {
    transform: translate(0, 150%);
  }
}

/*********************************************
 * CONVEX TRANSITION
 * Aliased 'default' for backwards compatibility
 *********************************************/

@each $stylename in default, convex {
  @include transition-stack(#{$stylename}) {
    transform-style: preserve-3d;
  }

  @include transition-horizontal-past(#{$stylename}) {
    transform: translate3d(-100%, 0, 0) rotateY(-90deg) translate3d(-100%, 0, 0);
  }
  @include transition-horizontal-future(#{$stylename}) {
    transform: translate3d(100%, 0, 0) rotateY(90deg) translate3d(100%, 0, 0);
  }
  @include transition-vertical-past(#{$stylename}) {
    transform: translate3d(0, -300px, 0) rotateX(70deg) translate3d(0, -300px, 0);
  }
  @include transition-vertical-future(#{$stylename}) {
    transform: translate3d(0, 300px, 0) rotateX(-70deg) translate3d(0, 300px, 0);
  }
}

/*********************************************
 * CONCAVE TRANSITION
 *********************************************/

@include transition-stack(concave) {
  transform-style: preserve-3d;
}

@include transition-horizontal-past(concave) {
  transform: translate3d(-100%, 0, 0) rotateY(90deg) translate3d(-100%, 0, 0);
}
@include transition-horizontal-future(concave) {
  transform: translate3d(100%, 0, 0) rotateY(-90deg) translate3d(100%, 0, 0);
}
@include transition-vertical-past(concave) {
  transform: translate3d(0, -80%, 0) rotateX(-70deg) translate3d(0, -80%, 0);
}
@include transition-vertical-future(concave) {
  transform: translate3d(0, 80%, 0) rotateX(70deg) translate3d(0, 80%, 0);
}


/*********************************************
 * ZOOM TRANSITION
 *********************************************/

@include transition-global(zoom) {
  transition-timing-function: ease;
}
@include transition-horizontal-past(zoom) {
  visibility: hidden;
  transform: scale(16);
}
@include transition-horizontal-future(zoom) {
  visibility: hidden;
  transform: scale(0.2);
}
@include transition-vertical-past(zoom) {
  transform: scale(16);
}
@include transition-vertical-future(zoom) {
  transform: scale(0.2);
}


/*********************************************
 * CUBE TRANSITION
 *
 * WARNING:
 * this is deprecated and will be removed in a
 * future version.
 *********************************************/

.reveal.cube .slides {
  perspective: 1300px;
}

.reveal.cube .slides section {
  padding: 30px;
  min-height: 700px;
  backface-visibility: hidden;
  box-sizing: border-box;
  transform-style: preserve-3d;
}
.reveal.center.cube .slides section {
  min-height: 0;
}
.reveal.cube .slides section:not(.stack):before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.1);
  border-radius: 4px;
  transform: translateZ( -20px );
}
.reveal.cube .slides section:not(.stack):after {
  content: '';
  position: absolute;
  display: block;
  width: 90%;
  height: 30px;
  left: 5%;
  bottom: 0;
  background: none;
  z-index: 1;

  border-radius: 4px;
  box-shadow: 0px 95px 25px rgba(0,0,0,0.2);
  transform: translateZ(-90px) rotateX( 65deg );
}

.reveal.cube .slides>section.stack {
  padding: 0;
  background: none;
}

.reveal.cube .slides>section.past {
  transform-origin: 100% 0%;
  transform: translate3d(-100%, 0, 0) rotateY(-90deg);
}

.reveal.cube .slides>section.future {
  transform-origin: 0% 0%;
  transform: translate3d(100%, 0, 0) rotateY(90deg);
}

.reveal.cube .slides>section>section.past {
  transform-origin: 0% 100%;
  transform: translate3d(0, -100%, 0) rotateX(90deg);
}

.reveal.cube .slides>section>section.future {
  transform-origin: 0% 0%;
  transform: translate3d(0, 100%, 0) rotateX(-90deg);
}


/*********************************************
 * PAGE TRANSITION
 *
 * WARNING:
 * this is deprecated and will be removed in a
 * future version.
 *********************************************/

.reveal.page .slides {
  perspective-origin: 0% 50%;
  perspective: 3000px;
}

.reveal.page .slides section {
  padding: 30px;
  min-height: 700px;
  box-sizing: border-box;
  transform-style: preserve-3d;
}
.reveal.page .slides section.past {
  z-index: 12;
}
.reveal.page .slides section:not(.stack):before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.1);
  transform: translateZ( -20px );
}
.reveal.page .slides section:not(.stack):after {
  content: '';
  position: absolute;
  display: block;
  width: 90%;
  height: 30px;
  left: 5%;
  bottom: 0;
  background: none;
  z-index: 1;

  border-radius: 4px;
  box-shadow: 0px 95px 25px rgba(0,0,0,0.2);

  -webkit-transform: translateZ(-90px) rotateX( 65deg );
}

.reveal.page .slides>section.stack {
  padding: 0;
  background: none;
}

.reveal.page .slides>section.past {
  transform-origin: 0% 0%;
  transform: translate3d(-40%, 0, 0) rotateY(-80deg);
}

.reveal.page .slides>section.future {
  transform-origin: 100% 0%;
  transform: translate3d(0, 0, 0);
}

.reveal.page .slides>section>section.past {
  transform-origin: 0% 0%;
  transform: translate3d(0, -40%, 0) rotateX(80deg);
}

.reveal.page .slides>section>section.future {
  transform-origin: 0% 100%;
  transform: translate3d(0, 0, 0);
}


/*********************************************
 * FADE TRANSITION
 *********************************************/

.reveal .slides section[data-transition=fade],
.reveal.fade .slides section:not([data-transition]),
.reveal.fade .slides>section>section:not([data-transition]) {
  transform: none;
  transition: opacity 0.5s;
}


.reveal.fade.overview .slides section,
.reveal.fade.overview .slides>section>section {
  transition: none;
}


/*********************************************
 * NO TRANSITION
 *********************************************/

@include transition-global(none) {
  transform: none;
  transition: none;
}


/*********************************************
 * PAUSED MODE
 *********************************************/

.reveal .pause-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  visibility: hidden;
  opacity: 0;
  z-index: 100;
  transition: all 1s ease;
}

.reveal .pause-overlay .resume-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
  color: #ccc;
  border-radius: 2px;
  padding: 6px 14px;
  border: 2px solid #ccc;
  font-size: 16px;
  background: transparent;
  cursor: pointer;

  &:hover {
    color: #fff;
    border-color: #fff;
  }
}

.reveal.paused .pause-overlay {
  visibility: visible;
  opacity: 1;
}


/*********************************************
 * FALLBACK
 *********************************************/

.no-transforms {
  overflow-y: auto;
}

.no-transforms .reveal {
  overflow: visible;
}

.no-transforms .reveal .slides {
  position: relative;
  width: 80%;
  max-width: 1280px;
  height: auto;
  top: 0;
  margin: 0 auto;
  text-align: center;
}

.no-transforms .reveal .controls,
.no-transforms .reveal .progress {
  display: none;
}

.no-transforms .reveal .slides section {
  display: block;
  opacity: 1;
  position: relative;
  height: auto;
  min-height: 0;
  top: 0;
  left: 0;
  margin: 10vh 0;
  margin: 70px 0;
  transform: none;
}

.reveal .no-transition,
.reveal .no-transition * {
  transition: none !important;
}


/*********************************************
 * PER-SLIDE BACKGROUNDS
 *********************************************/

.reveal .backgrounds {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  perspective: 600px;
}
.reveal .slide-background {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;

  background-color: rgba( 0, 0, 0, 0 );

  transition: all 800ms cubic-bezier(0.260, 0.860, 0.440, 0.985);
}

.reveal .slide-background-content {
  position: absolute;
  width: 100%;
  height: 100%;

  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.reveal .slide-background.stack {
  display: block;
}

.reveal .slide-background.present {
  opacity: 1;
  visibility: visible;
  z-index: 2;
}

.print-pdf .reveal .slide-background {
  opacity: 1 !important;
  visibility: visible !important;
}

/* Video backgrounds */
.reveal .slide-background video {
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: none;
  max-height: none;
  top: 0;
  left: 0;
  object-fit: cover;
}
.reveal .slide-background[data-background-size="contain"] video {
  object-fit: contain;
}

/* Immediate transition style */
.reveal[data-background-transition=none]>.backgrounds .slide-background,
.reveal>.backgrounds .slide-background[data-background-transition=none] {
  transition: none;
}

/* Slide */
.reveal[data-background-transition=slide]>.backgrounds .slide-background,
.reveal>.backgrounds .slide-background[data-background-transition=slide] {
  opacity: 1;
  backface-visibility: hidden;
}
.reveal[data-background-transition=slide]>.backgrounds .slide-background.past,
.reveal>.backgrounds .slide-background.past[data-background-transition=slide] {
  transform: translate(-100%, 0);
}
.reveal[data-background-transition=slide]>.backgrounds .slide-background.future,
.reveal>.backgrounds .slide-background.future[data-background-transition=slide] {
  transform: translate(100%, 0);
}

.reveal[data-background-transition=slide]>.backgrounds .slide-background>.slide-background.past,
.reveal>.backgrounds .slide-background>.slide-background.past[data-background-transition=slide] {
  transform: translate(0, -100%);
}
.reveal[data-background-transition=slide]>.backgrounds .slide-background>.slide-background.future,
.reveal>.backgrounds .slide-background>.slide-background.future[data-background-transition=slide] {
  transform: translate(0, 100%);
}


/* Convex */
.reveal[data-background-transition=convex]>.backgrounds .slide-background.past,
.reveal>.backgrounds .slide-background.past[data-background-transition=convex] {
  opacity: 0;
  transform: translate3d(-100%, 0, 0) rotateY(-90deg) translate3d(-100%, 0, 0);
}
.reveal[data-background-transition=convex]>.backgrounds .slide-background.future,
.reveal>.backgrounds .slide-background.future[data-background-transition=convex] {
  opacity: 0;
  transform: translate3d(100%, 0, 0) rotateY(90deg) translate3d(100%, 0, 0);
}

.reveal[data-background-transition=convex]>.backgrounds .slide-background>.slide-background.past,
.reveal>.backgrounds .slide-background>.slide-background.past[data-background-transition=convex] {
  opacity: 0;
  transform: translate3d(0, -100%, 0) rotateX(90deg) translate3d(0, -100%, 0);
}
.reveal[data-background-transition=convex]>.backgrounds .slide-background>.slide-background.future,
.reveal>.backgrounds .slide-background>.slide-background.future[data-background-transition=convex] {
  opacity: 0;
  transform: translate3d(0, 100%, 0) rotateX(-90deg) translate3d(0, 100%, 0);
}


/* Concave */
.reveal[data-background-transition=concave]>.backgrounds .slide-background.past,
.reveal>.backgrounds .slide-background.past[data-background-transition=concave] {
  opacity: 0;
  transform: translate3d(-100%, 0, 0) rotateY(90deg) translate3d(-100%, 0, 0);
}
.reveal[data-background-transition=concave]>.backgrounds .slide-background.future,
.reveal>.backgrounds .slide-background.future[data-background-transition=concave] {
  opacity: 0;
  transform: translate3d(100%, 0, 0) rotateY(-90deg) translate3d(100%, 0, 0);
}

.reveal[data-background-transition=concave]>.backgrounds .slide-background>.slide-background.past,
.reveal>.backgrounds .slide-background>.slide-background.past[data-background-transition=concave] {
  opacity: 0;
  transform: translate3d(0, -100%, 0) rotateX(-90deg) translate3d(0, -100%, 0);
}
.reveal[data-background-transition=concave]>.backgrounds .slide-background>.slide-background.future,
.reveal>.backgrounds .slide-background>.slide-background.future[data-background-transition=concave] {
  opacity: 0;
  transform: translate3d(0, 100%, 0) rotateX(90deg) translate3d(0, 100%, 0);
}

/* Zoom */
.reveal[data-background-transition=zoom]>.backgrounds .slide-background,
.reveal>.backgrounds .slide-background[data-background-transition=zoom] {
  transition-timing-function: ease;
}

.reveal[data-background-transition=zoom]>.backgrounds .slide-background.past,
.reveal>.backgrounds .slide-background.past[data-background-transition=zoom] {
  opacity: 0;
  visibility: hidden;
  transform: scale(16);
}
.reveal[data-background-transition=zoom]>.backgrounds .slide-background.future,
.reveal>.backgrounds .slide-background.future[data-background-transition=zoom] {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.2);
}

.reveal[data-background-transition=zoom]>.backgrounds .slide-background>.slide-background.past,
.reveal>.backgrounds .slide-background>.slide-background.past[data-background-transition=zoom] {
  opacity: 0;
  visibility: hidden;
  transform: scale(16);
}
.reveal[data-background-transition=zoom]>.backgrounds .slide-background>.slide-background.future,
.reveal>.backgrounds .slide-background>.slide-background.future[data-background-transition=zoom] {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.2);
}


/* Global transition speed settings */
.reveal[data-transition-speed="fast"]>.backgrounds .slide-background {
  transition-duration: 400ms;
}
.reveal[data-transition-speed="slow"]>.backgrounds .slide-background {
  transition-duration: 1200ms;
}


/*********************************************
 * OVERVIEW
 *********************************************/

.reveal.overview {
  perspective-origin: 50% 50%;
  perspective: 700px;

  .slides {
    // Fixes overview rendering errors in FF48+, not applied to
    // other browsers since it degrades performance
    -moz-transform-style: preserve-3d;
  }

  .slides section {
    height: 100%;
    top: 0 !important;
    opacity: 1 !important;
    overflow: hidden;
    visibility: visible !important;
    cursor: pointer;
    box-sizing: border-box;
  }
  .slides section:hover,
  .slides section.present {
    outline: 10px solid rgba(150,150,150,0.4);
    outline-offset: 10px;
  }
  .slides section .fragment {
    opacity: 1;
    transition: none;
  }
  .slides section:after,
  .slides section:before {
    display: none !important;
  }
  .slides>section.stack {
    padding: 0;
    top: 0 !important;
    background: none;
    outline: none;
    overflow: visible;
  }

  .backgrounds {
    perspective: inherit;

    // Fixes overview rendering errors in FF48+, not applied to
    // other browsers since it degrades performance
    -moz-transform-style: preserve-3d;
  }

  .backgrounds .slide-background {
    opacity: 1;
    visibility: visible;

    // This can't be applied to the slide itself in Safari
    outline: 10px solid rgba(150,150,150,0.1);
    outline-offset: 10px;
  }

  .backgrounds .slide-background.stack {
    overflow: visible;
  }
}

// Disable transitions transitions while we're activating
// or deactivating the overview mode.
.reveal.overview .slides section,
.reveal.overview-deactivating .slides section {
  transition: none;
}

.reveal.overview .backgrounds .slide-background,
.reveal.overview-deactivating .backgrounds .slide-background {
  transition: none;
}


/*********************************************
 * RTL SUPPORT
 *********************************************/

.reveal.rtl .slides,
.reveal.rtl .slides h1,
.reveal.rtl .slides h2,
.reveal.rtl .slides h3,
.reveal.rtl .slides h4,
.reveal.rtl .slides h5,
.reveal.rtl .slides h6 {
  direction: rtl;
  font-family: sans-serif;
}

.reveal.rtl pre,
.reveal.rtl code {
  direction: ltr;
}

.reveal.rtl ol,
.reveal.rtl ul {
  text-align: right;
}

.reveal.rtl .progress span {
  float: right
}

/*********************************************
 * PARALLAX BACKGROUND
 *********************************************/

.reveal.has-parallax-background .backgrounds {
  transition: all 0.8s ease;
}

/* Global transition speed settings */
.reveal.has-parallax-background[data-transition-speed="fast"] .backgrounds {
  transition-duration: 400ms;
}
.reveal.has-parallax-background[data-transition-speed="slow"] .backgrounds {
  transition-duration: 1200ms;
}


