/*********************************************
 * PLAYBACK COMPONENT
 *********************************************/

.reveal .playback {
  position: absolute;
  left: 15px;
  bottom: 20px;
  z-index: 30;
  cursor: pointer;
  transition: all 400ms ease;
  -webkit-tap-highlight-color: rgba( 0, 0, 0, 0 );
}

.reveal.overview .playback {
  opacity: 0;
  visibility: hidden;
}


/*********************************************
 * CODE HIGHLGIHTING
 *********************************************/

.reveal .hljs table {
  margin: initial;
}

.reveal .hljs-ln-code,
.reveal .hljs-ln-numbers {
  padding: 0;
  border: 0;
}

.reveal .hljs-ln-numbers {
  opacity: 0.6;
  padding-right: 0.75em;
  text-align: right;
  vertical-align: top;
}

.reveal .hljs[data-line-numbers]:not([data-line-numbers=""]) tr:not(.highlight-line) {
  opacity: 0.4;
}


/*********************************************
 * ROLLING LINKS
 *********************************************/

.reveal .roll {
  display: inline-block;
  line-height: 1.2;
  overflow: hidden;

  vertical-align: top;
  perspective: 400px;
  perspective-origin: 50% 50%;
}
.reveal .roll:hover {
  background: none;
  text-shadow: none;
}
.reveal .roll span {
  display: block;
  position: relative;
  padding: 0 2px;

  pointer-events: none;
  transition: all 400ms ease;
  transform-origin: 50% 0%;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}
.reveal .roll:hover span {
  background: rgba(0,0,0,0.5);
  transform: translate3d( 0px, 0px, -45px ) rotateX( 90deg );
}
.reveal .roll span:after {
  content: attr(data-title);

  display: block;
  position: absolute;
  left: 0;
  top: 0;
  padding: 0 2px;
  backface-visibility: hidden;
  transform-origin: 50% 0%;
  transform: translate3d( 0px, 110%, 0px ) rotateX( -90deg );
}


/*********************************************
 * SPEAKER NOTES
 *********************************************/

$notesWidthPercent: 25%;

// Hide on-page notes
.reveal aside.notes {
  display: none;
}

// An interface element that can optionally be used to show the
// speaker notes to all viewers, on top of the presentation
.reveal .speaker-notes {
  display: none;
  position: absolute;
  width: $notesWidthPercent / (1-$notesWidthPercent/100) * 1%;
  height: 100%;
  top: 0;
  left: 100%;
  padding: 14px 18px 14px 18px;
  z-index: 1;
  font-size: 18px;
  line-height: 1.4;
  border: 1px solid rgba( 0, 0, 0, 0.05 );
  color: #222;
  background-color: #f5f5f5;
  overflow: auto;
  box-sizing: border-box;
  text-align: left;
  font-family: Helvetica, sans-serif;
  -webkit-overflow-scrolling: touch;

  .notes-placeholder {
    color: #ccc;
    font-style: italic;
  }

  &:focus {
    outline: none;
  }

  &:before {
    content: 'Speaker notes';
    display: block;
    margin-bottom: 10px;
    opacity: 0.5;
  }
}


.reveal.show-notes {
  max-width: 100% - $notesWidthPercent;
  overflow: visible;
}

.reveal.show-notes .speaker-notes {
  display: block;
}

@media screen and (min-width: 1600px) {
  .reveal .speaker-notes {
    font-size: 20px;
  }
}

@media screen and (max-width: 1024px) {
  .reveal.show-notes {
    border-left: 0;
    max-width: none;
    max-height: 70%;
    max-height: 70vh;
    overflow: visible;
  }

  .reveal.show-notes .speaker-notes {
    top: 100%;
    left: 0;
    width: 100%;
    height: (30/0.7)*1%;
    height: 30vh;
    border: 0;
  }
}

@media screen and (max-width: 600px) {
  .reveal.show-notes {
    max-height: 60%;
    max-height: 60vh;
  }

  .reveal.show-notes .speaker-notes {
    top: 100%;
    height: (40/0.6)*1%;
    height: 40vh;
  }

  .reveal .speaker-notes {
    font-size: 14px;
  }
}


/*********************************************
 * ZOOM PLUGIN
 *********************************************/

.zoomed .reveal *,
.zoomed .reveal *:before,
.zoomed .reveal *:after {
  backface-visibility: visible !important;
}

.zoomed .reveal .progress,
.zoomed .reveal .controls {
  opacity: 0;
}

.zoomed .reveal .roll span {
  background: none;
}

.zoomed .reveal .roll span:after {
  visibility: hidden;
}
